import React, { useRef, useEffect, useState } from "react";

//TOP IMGS
import cover_img from './imgs/cover_img.png';
import text_01 from './imgs/text_01.png';
import invite_img from './imgs/invite_img.png';
import text_02 from './imgs/text_02.png';
import image_07 from './imgs/image_07.png';
import image_08 from './imgs/image_08.png';
import image_09 from './imgs/image_09.png';

//BOTTOM IMGS
import image_02 from './imgs/image_02.png';

import image_03 from './imgs/image_03.png';
import image_04 from './imgs/image_04.png';

import image_05 from './imgs/image_05.png';
import image_06 from './imgs/image_06.png';

import recuadro_01 from './imgs/recuadro_01.png';
import recuadro_02 from './imgs/recuadro_02.png';
import recuadro_03 from './imgs/recuadro_03.png';
import { Parallax } from 'react-scroll-parallax';

import useMeasure from 'react-use-measure'

import './Mobile.css';

const Mobile = () => {
  const [scrollTop, setScrollTop] = useState(0);
  const viewPortWidth = window.innerWidth;
  const viewPortHeight = window.innerHeight;

  const invite_img_height = ( viewPortWidth * 972 * 0.6 ) / 520;
  const text_02_height = ( viewPortWidth * 972 * 0.6 ) / 520;
  const whitespace_half_height = viewPortHeight * 0.5;
  const image_03_height = viewPortWidth * 1126 / 520;
  const image_03_half_height = viewPortWidth * 1126 * 0.4333 / 520;
  const three_times_viewPortHeight = viewPortHeight * 3;

  const [totalHeightref, bounds] = useMeasure()

  const whitespace_01Ref = useRef(null); // WHITESPACE
  const invite_imgRef = useRef(null);
  const whitespace_02Ref = useRef(null); // WHITESPACE
  const whitespace_03Ref = useRef(null); // WHITESPACE
  const whitespace_04Ref = useRef(null); // WHITESPACE
  const text_02Ref = useRef(null);
  const whitespace_05Ref = useRef(null); // WHITESPACE
  const whitespace_06Ref = useRef(null); // WHITESPACE
  const whitespace_07Ref = useRef(null); // WHITESPACE
  const image_07Ref = useRef(null);

  const [ whitespace_01_isVisible, setWhitespace_01_isVisible ] = useState(false); // WHITESPACE
  const [ invite_img_isVisible, setInvite_img_isVisible ] = useState(false);
  const [ whitespace_02_isVisible, setWhitespace_02_isVisible ] = useState(false); // WHITESPACE
  const [ whitespace_03_isVisible, setWhitespace_03_isVisible ] = useState(false); // WHITESPACE
  const [ whitespace_04_isVisible, setWhitespace_04_isVisible ] = useState(false); // WHITESPACE
  const [ text_02_isVisible, setText_02_isVisible ] = useState(false);
  const [ whitespace_05_isVisible, setWhitespace_05_isVisible ] = useState(false); // WHITESPACE
  const [ whitespace_06_isVisible, setWhitespace_06_isVisible ] = useState(false); // WHITESPACE
  const [ whitespace_07_isVisible, setWhitespace_07_isVisible ] = useState(false); // WHITESPACE
  const [ image_07_isVisible, setImage_07_isVisible ] = useState(false);

  const [clip_01, setClip_01] = useState(0);
  const [clip_02, setClip_02] = useState(0);
  const [opacity_01, setOpacity_01] = useState(1);
  const [opacity_02, setOpacity_02] = useState(1);

  const [scale_01, setScale_01] = useState(1);
  const [scale_02, setScale_02] = useState(1);
  const [scale_03, setScale_03] = useState(1);
  const [scale_04, setScale_04] = useState(1);

  const [display_01, setDisplay_01] = useState("block");
  const [display_02, setDisplay_02] = useState("block");
  const [display_03, setDisplay_03] = useState("block");
  const [display_04, setDisplay_04] = useState("block");
  const [display_05, setDisplay_05] = useState("block");

  const [sticky_06_zIndex, setSticky_06_zIndex] = useState(-1000);
  const [display_06, setDisplay_06] = useState("none");

  const whitespace_01_callback = (entries) => {
    const [ entry ] = entries;
    setWhitespace_01_isVisible(entry.isIntersecting); // WHITESPACE
  }
  const invite_img_callback = (entries) => {
    const [ entry ] = entries;
    setInvite_img_isVisible(entry.isIntersecting);
  }
  const whitespace_02_callback = (entries) => {
    const [ entry ] = entries;
    setWhitespace_02_isVisible(entry.isIntersecting); // WHITESPACE
  }
  const whitespace_03_callback = (entries) => {
    const [ entry ] = entries;
    setWhitespace_03_isVisible(entry.isIntersecting); // WHITESPACE
  }
  const whitespace_04_callback = (entries) => {
    const [ entry ] = entries;
    setWhitespace_04_isVisible(entry.isIntersecting); // WHITESPACE
  }
  const text_02_callback = (entries) => {
    const [ entry ] = entries;
    setText_02_isVisible(entry.isIntersecting);
  }
  const whitespace_05_callback = (entries) => {
    const [ entry ] = entries;
    setWhitespace_05_isVisible(entry.isIntersecting); // WHITESPACE
  }
  const whitespace_06_callback = (entries) => {
    const [ entry ] = entries;
    setWhitespace_06_isVisible(entry.isIntersecting); // WHITESPACE
  }
  const whitespace_07_callback = (entries) => {
    const [ entry ] = entries;
    setWhitespace_07_isVisible(entry.isIntersecting); // WHITESPACE
  }
  const image_07_callback = (entries) => {
    const [ entry ] = entries;
    setImage_07_isVisible(entry.isIntersecting);
  }
  
  useEffect(() => {
    const options_topBottom = { root: null, rootMargin: "25% 0px 25% 0px", threshold: 0.5 }
    const options = { root: null, rootMargin: "0px", threshold: 0.3 }
    const whitespace_options = { root: null, rootMargin: "0px", threshold: 0.5 }
    const image_07_options = { root: null, rootMargin: "0px", threshold: "0.0" }

    const whitespace_01_obs = new IntersectionObserver(whitespace_01_callback, options_topBottom); // WHITESPACE
    const invite_img_obs = new IntersectionObserver(invite_img_callback, options);
    const whitespace_02_obs = new IntersectionObserver(whitespace_02_callback, options_topBottom); // WHITESPACE
    const whitespace_03_obs = new IntersectionObserver(whitespace_03_callback, whitespace_options); // WHITESPACE
    const whitespace_04_obs = new IntersectionObserver(whitespace_04_callback, options_topBottom); // WHITESPACE
    const text_02_obs = new IntersectionObserver(text_02_callback, options);
    const whitespace_05_obs = new IntersectionObserver(whitespace_05_callback, options_topBottom); // WHITESPACE
    const whitespace_06_obs = new IntersectionObserver(whitespace_06_callback, whitespace_options); // WHITESPACE
    const whitespace_07_obs = new IntersectionObserver(whitespace_07_callback, options_topBottom); // WHITESPACE
    const image_07_obs = new IntersectionObserver(image_07_callback, image_07_options);

    let whitespace_01RefValue = null;
    let invite_imgRefValue = null;
    let whitespace_02RefValue = null;
    let whitespace_03RefValue = null;
    let whitespace_04RefValue = null;
    let text_02RefValue = null;
    let whitespace_05RefValue = null;
    let whitespace_06RefValue = null;
    let whitespace_07RefValue = null;
    let image_07RefValue = null;

    if (whitespace_01Ref.current) whitespace_01_obs.observe(whitespace_01Ref.current) // WHITESPACE
    if (invite_imgRef.current) invite_img_obs.observe(invite_imgRef.current)
    if (whitespace_02Ref.current) whitespace_02_obs.observe(whitespace_02Ref.current) // WHITESPACE
    if (whitespace_03Ref.current) whitespace_03_obs.observe(whitespace_03Ref.current) // WHITESPACE
    if (whitespace_04Ref.current) whitespace_04_obs.observe(whitespace_04Ref.current) // WHITESPACE
    if (text_02Ref.current) text_02_obs.observe(text_02Ref.current)
    if (whitespace_05Ref.current) whitespace_05_obs.observe(whitespace_05Ref.current) // WHITESPACE
    if (whitespace_06Ref.current) whitespace_06_obs.observe(whitespace_06Ref.current) // WHITESPACE
    if (whitespace_07Ref.current) whitespace_07_obs.observe(whitespace_07Ref.current) // WHITESPACE
    if (image_07Ref.current) image_07_obs.observe(image_07Ref.current)

    whitespace_01RefValue =  whitespace_01Ref.current;
    invite_imgRefValue = invite_imgRef.current;
    whitespace_02RefValue = whitespace_02Ref.current;
    whitespace_03RefValue = whitespace_03Ref.current;
    whitespace_04RefValue = whitespace_04Ref.current;
    text_02RefValue = text_02Ref.current;
    whitespace_05RefValue = whitespace_05Ref.current;
    whitespace_06RefValue = whitespace_06Ref.current;
    whitespace_07RefValue = whitespace_07Ref.current;
    image_07RefValue = image_07Ref.current;

  return () => {
    if (whitespace_01RefValue) whitespace_01_obs.unobserve(whitespace_01RefValue) // WHITESPACE
    if (invite_imgRefValue) invite_img_obs.unobserve(invite_imgRefValue)
    if (whitespace_02RefValue) whitespace_02_obs.unobserve(whitespace_02RefValue) // WHITESPACE
    if (whitespace_03RefValue) whitespace_03_obs.unobserve(whitespace_03RefValue) // WHITESPACE
    if (whitespace_04RefValue) whitespace_04_obs.unobserve(whitespace_04RefValue) // WHITESPACE
    if (text_02RefValue) text_02_obs.unobserve(text_02RefValue)
    if (whitespace_05RefValue) whitespace_05_obs.unobserve(whitespace_05RefValue) // WHITESPACE
    if (whitespace_06RefValue) whitespace_06_obs.unobserve(whitespace_06RefValue) // WHITESPACE
    if (whitespace_07RefValue) whitespace_07_obs.unobserve(whitespace_07RefValue) // WHITESPACE
    if (image_07RefValue) image_07_obs.unobserve(image_07RefValue)
    }
  }, [
        whitespace_01Ref,
        invite_imgRef,
        whitespace_02Ref,
        whitespace_03Ref,
        whitespace_04Ref,
        text_02Ref,
        whitespace_05Ref,
        whitespace_06Ref,
        whitespace_07Ref,
        image_07Ref
    ]);

   useEffect(() => {
    const handleScroll = (event) => {
      const invite_img_y = invite_imgRef.current.offsetTop;
      const whitespace_01_y = whitespace_01Ref.current.offsetTop;
      const whitespace_02_y = whitespace_02Ref.current.offsetTop;
      const whitespace_03_y = whitespace_03Ref.current.offsetTop;
      const whitespace_04_y = whitespace_04Ref.current.offsetTop;
      const whitespace_05_y = whitespace_05Ref.current.offsetTop;
      const whitespace_07_y = whitespace_07Ref.current.offsetTop;
      const text_02_y = text_02Ref.current.offsetTop;
      const whitespace_06_y = whitespace_06Ref.current.offsetTop;
      setScrollTop(window.scrollY);

      const scrollTop_plus_half_whitespace = scrollTop + whitespace_half_height;
      const scrollTop_plus_half_image_03 = scrollTop + image_03_half_height;

      const clipValue_01 = scrollTop - invite_img_y + invite_img_height;
      const clipValue_02 = scrollTop - text_02_y + text_02_height;
      const opacityValue_01 = (viewPortHeight - (scrollTop_plus_half_whitespace - whitespace_03_y)) / viewPortHeight;
      const opacityValue_02 = (viewPortHeight - (scrollTop_plus_half_whitespace - whitespace_06_y)) / viewPortHeight;

      
      if (whitespace_04_isVisible || scrollTop >= whitespace_04_y) { 
      }
      if (whitespace_05_isVisible) { 
      }
      if (whitespace_07_isVisible || scrollTop >= whitespace_07_y) { 
      }
      if (whitespace_01_isVisible) { 
        const scaleValue_01 = 1.0566 + ((scrollTop_plus_half_whitespace - whitespace_01_y) * 0.1551774714 / viewPortHeight);
        setScale_01((scaleValue_01 >= 1) ? scaleValue_01 : 1);
      }  
      if (invite_img_isVisible) { 
        setClip_01(((clipValue_01 >= 0) ? clipValue_01 : 0)); 
      }
      if (whitespace_02_isVisible) { 
        setDisplay_01("none");
        setDisplay_03("none");
      }
      if (whitespace_03_isVisible) { 
        setOpacity_01(((opacityValue_01 >= 0) ? opacityValue_01 : 0)); 
      }
      if (whitespace_02_isVisible || whitespace_03_isVisible || whitespace_04_isVisible) { 
        const scaleValue_02 = 1.0187 + ((scrollTop_plus_half_image_03 - whitespace_02_y) * 0.1994720965 / three_times_viewPortHeight);
        setScale_02((scaleValue_02 >= 1) ? scaleValue_02 : 1);
        setClip_01(image_03_height * 1.1111);
      }
      if (whitespace_03_isVisible || whitespace_04_isVisible || text_02_isVisible) { 
        const scaleValue_03 = 0.9987 + ((scrollTop_plus_half_image_03 - whitespace_03_y) * 0.2176059235 / three_times_viewPortHeight);
        setScale_03((scaleValue_03 >= 1) ? scaleValue_03 : 1);
      }
      if (text_02_isVisible) { 
        setClip_02(((clipValue_02 >= 0) ? clipValue_02 : 0)); 
      }
      if (whitespace_06_isVisible) { 
        setOpacity_02(((opacityValue_02 >= 0) ? opacityValue_02 : 0)); 
      }
      if (whitespace_05_isVisible || whitespace_06_isVisible || whitespace_07_isVisible) { 
        const scaleValue_04 = 1.0191 + ((scrollTop_plus_half_image_03 - whitespace_05_y) * 0.1975354742 / three_times_viewPortHeight);
        setScale_04((scaleValue_04 >= 1) ? scaleValue_04 : 1);
        setClip_02(image_03_height * 1.1111); 
      }
      if (image_07_isVisible) { 
        setSticky_06_zIndex(1000);
        setDisplay_06("block");
      } else { 
        setSticky_06_zIndex(-1000);
        setDisplay_06("none");
      }
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [
      invite_img_height,
      text_02_height,
      viewPortHeight,
      whitespace_half_height,
      image_03_height,
      image_03_half_height,
      three_times_viewPortHeight,
      scrollTop,
      whitespace_01_isVisible,
      invite_img_isVisible,
      whitespace_02_isVisible,
      whitespace_03_isVisible,
      whitespace_04_isVisible,
      text_02_isVisible,
      whitespace_05_isVisible,
      whitespace_06_isVisible,
      whitespace_07_isVisible,
      image_07_isVisible,
      whitespace_01Ref,
      invite_imgRef,
      whitespace_02Ref,
      whitespace_03Ref,
      whitespace_04Ref,
      text_02Ref,
      whitespace_05Ref,
      whitespace_06Ref,
      whitespace_07Ref,
      image_07Ref,
      display_01,
      display_02,
      display_03,
      display_04,
      display_05
  ]);

return (
    <div ref={totalHeightref}>

    <div className="mobile_bottom_container">

    <img src={image_02} className="mobile_bottom_img" style={{
                                                              /*display: display_01,*/
                                                              position: "fixed",
                                                              width: "100%",
                                                              marginLeft: "0em",
                                                              zIndex: "-100",
                                                              clipPath: "inset(0px 0px " + clip_01 + "px 0px",
                                                              transform: "scale(" + scale_01 + ")"
                                                            }} alt=''/>
    <img src={image_03} className="mobile_bottom_img" style={{
                                                              /*display: display_02,*/
                                                              position: "fixed",
                                                              width: "100%",
                                                              marginLeft: "0em",
                                                              zIndex: "-200",
                                                              opacity: opacity_01,
                                                              transform: "scale(" + scale_02 + ")"
                                                            }} alt=''/>
    <img src={image_04} className="mobile_bottom_img" style={{
                                                              /*display: display_03,*/
                                                              position: "fixed",
                                                              width: "100%",
                                                              marginLeft: "0em",
                                                              zIndex: "-300",
                                                              clipPath: "inset(0px 0px " + clip_02 + "px 0px",
                                                              transform: "scale(" + scale_02 + ")"
                                                            }} alt=''/> 
    <img src={image_05} className="mobile_bottom_img" style={{
                                                              /*display: display_04,*/
                                                              position: "fixed",
                                                              width: "100%",
                                                              marginLeft: "0em",
                                                              zIndex: "-400",
                                                              opacity: opacity_02,
                                                              transform: "scale(" + scale_04 + ")"
                                                            }} alt=''/>
    <img src={image_06} className="mobile_bottom_img" style={{
                                                              /*display: display_05,*/
                                                              position: "fixed",
                                                              width: "100%",
                                                              marginLeft: "0em",
                                                              zIndex: "-500"
                                                            }} alt=''/>
    </div>

    <img src={cover_img} style={{position: "relative", zIndex: "1000", width: "100%", marginLeft: "0em", marginBottom: "-1%"}} alt=''/>
    <img src={text_01} style={{position: "relative", zIndex: "1000", width: "100%", marginLeft: "0em"}} alt=''/>

    <div ref={whitespace_01Ref} className="mobile_whitespace" style={{height: viewPortHeight + "px"}}>
        <div style={{width: "100%", height: viewPortHeight + "px", padding: "0", margin: "0", background: "transparent"}}/>
    </div>

    <img ref={invite_imgRef} src={invite_img} style={{position: "relative", zIndex: "1000", width: "100%", marginLeft: "0em"}} alt=''/>

    <div ref={whitespace_02Ref} className="mobile_whitespace" style={{height: viewPortHeight + "px"}}>
        <div style={{width: "100%", height: viewPortHeight + "px", padding: "0", margin: "0", background: "transparent"}}>
            <Parallax speed={45}>
                <img src={recuadro_01} className="recuadro" style={{marginTop: "180%", marginLeft: "calc(15% + 8%)"}} alt=''/>
            </Parallax>
        </div>
    </div>

    <div ref={whitespace_03Ref} className="mobile_whitespace" style={{height: viewPortHeight + "px"}}>
        <div style={{width: "100%", height: viewPortHeight + "px", padding: "0", margin: "0", background: "transparent"}}>
            <Parallax speed={45}>
                <img src={recuadro_02} className="recuadro" style={{marginTop: "180%", marginLeft: "calc(15% - 8%)"}} alt=''/>
            </Parallax>
        </div>
    </div>

    <div ref={whitespace_04Ref} className="mobile_whitespace" style={{height: viewPortHeight + "px"}}>
        <div style={{width: "100%", height: viewPortHeight + "px", padding: "0", margin: "0", background: "transparent"}}/>
    </div>

    <img ref={text_02Ref} src={text_02} style={{position: "relative", zIndex: "1000", width: "100%", marginLeft: "0em"}} alt=''/>

    <div ref={whitespace_05Ref} className="mobile_whitespace" style={{height: viewPortHeight + "px"}}>
        <div style={{width: "100%", height: viewPortHeight + "px", padding: "0", margin: "0", background: "transparent"}}/>
    </div>

    <div ref={whitespace_06Ref} className="mobile_whitespace" style={{height: viewPortHeight + "px"}}>
        <div style={{width: "100%", height: viewPortHeight + "px", padding: "0", margin: "0", background: "transparent"}}>
            <Parallax speed={45}>
                <img src={recuadro_03} className="recuadro" style={{marginTop: "0%", marginLeft: "15%"}} alt=''/>
            </Parallax>
        </div>
    </div>

    <div ref={whitespace_07Ref} className="mobile_whitespace" style={{height: viewPortHeight + "px"}}>
        <img src={image_06} style={{position: "relative", zIndex: sticky_06_zIndex, display: display_06, width: "100%", marginLeft: "0em"}} alt=''/>
    </div>

    <img ref={image_07Ref} src={image_07} style={{position: "relative", zIndex: "1000", width: "100%", marginLeft: "0em", marginBottom: "-1%"}} alt=''/>
    <img src={image_08} style={{position: "relative", zIndex: "1000", width: "100%", marginLeft: "0em", marginBottom: "-1%"}} alt=''/>
    <img src={image_09} style={{position: "relative", zIndex: "1000", width: "100%", marginLeft: "0em", marginBottom: "-1%"}} alt=''/>
    </div>
);
};

export default Mobile;